.add_panel {
  background-color: #333;
  width: 100%;
  min-height: 100vh;
  padding-top: 5rem;
}

.adding_form {
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  padding-bottom: 4rem;
  position: relative;
}

.add_panel .adding_form {
  width: 70%;
  margin: 10rem auto;
}

.loader_success {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 10000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s;
}

.loader_success.show_on_success {
  visibility: visible;
  opacity: 1;
}

.loader_success svg {
  width: 20rem;
  height: 20rem;
}

.loader_success g {
  stroke-width: 2rem;
  --line: 46760px;
  fill: transparent;
  stroke-linecap: round;
  stroke: #fff;
  -webkit-animation: icon_appear 2s cubic-bezier(0.52, 0, 1, 0.25) alternate
    infinite;
  animation: icon_appear 2s cubic-bezier(0.52, 0, 1, 0.25) alternate infinite;
}

.f-2 {
  font-size: 2rem;
}

.btn_animate {
  position: relative;
  color: var(--color-1);
  padding: 2rem;
  font-size: 1.6rem;
  letter-spacing: 0.2rem;
  background-color: #333 !important;
  box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.5);
  overflow: hidden;
  text-decoration: none;
  border: none;
  margin-bottom: 1rem;
}
.btn_animate::before {
  content: "";
  position: absolute;
  top: 0.2rem;
  left: 0.2rem;
  bottom: 0.2rem;
  width: 50%;
  background: rgba(255, 255, 255, 0.05);
}

.btn_animate > span:nth-child(1) {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.2rem;
  background: linear-gradient(to right, #333, var(--color-1));
  -webkit-animation: animate1 2s linear infinite;
  animation: animate1 2s linear infinite;
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}

#description {
  height: auto !important;
}
@-webkit-keyframes animate1 {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
@keyframes animate1 {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.adding_form label,
.adding_form input,
.adding_form textarea {
  font-size: 1.6rem;
}

.input_animate {
  padding: 0;
}

.btn_wrapper {
  align-items: center;
}

