.btn_wrapper {
  display: flex;
  justify-content: space-evenly;
}

.jodit-container {
  border: none !important;
}

.jodit_theme_black {
  background-color: #333 !important;
}

.jodit_theme_black .jodit-toolbar__box {
  background-color: #2e2e2e !important;
}
.jodit_theme_black button svg,
.jodit_theme_black .jodit-toolbar-button__trigger svg {
  fill: var(--color-1) !important;
}
.jodit_theme_black button:hover,
.jodit_theme_black .jodit-toolbar-button__trigger:hover {
  background-color: #303030 !important;
}

.jodit-toolbar__box:not(:empty) {
  border-bottom: none !important;
}

.jodit_theme_black button:hover svg,
.jodit_theme_black .jodit-toolbar-button__trigger:hover svg {
  fill: #fff !important;
}
.jodit_theme_black .jodit-status-bar {
  background-color: #3f3f3f;
}
.jodit-toolbar-editor-collection {
  background-image: none !important;
  border-bottom: 1px solid var(--color-1);
}
.jodit-toolbar-editor-collection::after {
  height: 0 !important;
}

span.jodit-toolbar-button.jodit-toolbar-button_size_middle.jodit-toolbar-button_dots {
  display: none !important;
}

.jodit-workplace + .jodit-status-bar:not(:empty) {
  border-top: 1px solid var(--color-1) !important;
}

.jodit_theme_black ol,
.jodit_theme_black ul {
  padding-left: 2rem;
  margin-left: 4rem;
}

.jodit_theme_black ul {
  list-style: initial;
}

.jodit_theme_black li {
  list-style-type: inherit !important;
  list-style: inherit;
}

@media only screen and (max-width: 660px) {
  .btn_wrapper {
    flex-direction: column;
  }
}

.jodit-wysiwyg {
  color: #fff !important;
}
.jodit-wysiwyg * {
  color: #fff !important;
}
